import { ExtensionContext, window, workspace, WorkspaceFolder } from 'vscode'

// #region CommandConstructor: æ¥å£ - å‘½ä»¤æ„é€ å‡½æ•°
interface CommandConstructor {
  new (): any
}
// #endregion

// #region registrableCommands: å¯æ³¨å†Œçš„å‘½ä»¤æ„é€ å‡½æ•°åˆ—è¡¨
const registrableCommands: CommandConstructor[] = []
// #endregion

// #region command: ä¿®é¥°å™¨ - å‘½ä»¤ç±»
/**
 * ç±»ä¿®é¥°å™¨
 * @export
 * @returns {ClassDecorator}
 */
export function command(): ClassDecorator {
  return (target: any) => {
    registrableCommands.push(target)
  }
}
// #endregion

// #region registerCommands: æ³¨å†Œå‘½ä»¤
/**
 * æ³¨å†Œå‘½ä»¤
 * @export
 * @param {ExtensionContext} context
 */
export function registerCommands(context: ExtensionContext) {
  for (const c of registrableCommands) {
    context.subscriptions.push(new c())
  }
}
// #endregion

// #region Commands: æšä¸¾ - å‘½ä»¤
export enum Commands {
  tag = 'extension.tag',
}
// #endregion

// #region QuickPickItem: æ¥å£ - å¯é€‰åˆ—è¡¨é¡¹æ•°æ®ç»“æ„
export interface QuickPickItem {
  label: string // å‘½ä»¤
  description?: string // å‘½ä»¤æè¿°
  path?: string // å·¥ç¨‹è·¯å¾„
  versionName?: string // é…ç½®ä¸åŒç¯å¢ƒçš„versionå±æ€§å
}
// #endregion

// #region showQuickPick: æ˜¾ç¤ºå¯é€‰é¡¹åˆ—è¡¨
/**
 * æ˜¾ç¤ºé€‰æ¡†
 * @param {QuickPickItem[]} QuickPickList
 * @returns
 */
export async function showQuickPick(QuickPickList: QuickPickItem[]) {
  return new Promise<QuickPickItem>((resolve, reject) => {
    try {
      window.showQuickPick(QuickPickList).then(command => {
        console.log('TCL: showQuickPick -> command', command)
        resolve(command)
      })
    } catch (err) {
      console.log('TCL: catch -> err', err)
      reject(err)
    }
  })
}
// #endregion

// #region getWorkspaceFolders: è·å–ç›®å½•åˆ—è¡¨
/**
 * è·å–ç›®å½•åˆ—è¡¨
 */
export function getWorkspaceFolders() {
  const workspaceFolders: WorkspaceFolder[] = workspace.workspaceFolders || []
  console.log('TCL: Tag -> getWorkspaceFolders -> workspaceFolders', workspaceFolders)
  console.log('TCL: Tag -> getWorkspaceFolders -> vscode.workspace.rootPath', workspace.rootPath)
  return workspaceFolders.map(folder => {
    return {
      label: folder.name,
      path: folder.uri.path,
    }
  })
}
// #endregion
